.sketch-container {
    background-color: #5ADF8E;
    padding:1rem 5rem 5rem 5rem;
    min-height:100vh;
}
.sketch{
    font-size: 1.4rem;
    font-weight: 700;
    letter-spacing: .1rem;
    margin-bottom: 3rem;
}

.bodyText {
    letter-spacing: .1rem;
    line-height:1.5rem;
    margin-bottom:1.5rem;
}

.sketchContainer, .sketchHeader {
    width:100%;
}
.sketchContainer .leftHeader img, .sketchContainer .rightHeader img {
    width:55px;
    height:55px;
    float:right;
    margin-right:2.5rem;
}
.sketchContainer .leftHeader, .sketchContainer .rightHeader {
    font-size: 1.3rem;
    line-height:4rem;
    font-weight:700;
    letter-spacing: .1rem;
    text-align: left;
    margin-left:2.5rem;
}
.sketchContainer .inner, .sketchHeader .inner {
    width:50%;
    float:left;
}
.sketchContainer .inner {
    text-align: center;
    margin-bottom:1rem;
}
.sketchHeader .inner {
    position:relative;
    left:2.5rem;
    font-size: 1.3rem;
    line-height:4rem;
    font-weight:700;
    letter-spacing: .1rem;
}

.sketchHeader .inner img {
    width:50px;
    height:50px;
    float:right;
    margin-right:5rem;
    position:relative;
    top:.4rem;
}


.innerBorder {
    border-radius: 3rem;
}

.innerBorder.left{
    margin-right:1%;
}
.innerBorder.right{
    margin-left:1%;
}

.innerBorder img {
    border-radius: 3rem;
    max-width:100%;
}

.innerBorder.right img {
    max-width:100%;
}


.clear {
    clear:both;
}

.toolsContainer {
    border-radius: 2rem;
    padding: 2rem;
    letter-spacing: .1rem;
    background-color:rgba(255,255,255,.5);
    margin-top:2%;
}

.toolsContainer .header {
    font-weight:600;
}


p {
    padding-top:4%
}

@media only screen and (max-width: 1200px) {
    .sketchContainer .inner, .sketchHeader .inner {
        width:100%;
    }
    .innerBorder.left, innerBorder.right, .innerBorder.left img, .innerBorder.right img {
        width:100%;
    }
    .innerBorder.left{
        margin-right:0%;
    }
    .innerBorder.right{
        margin-left:0%;
    }
    .sketchContainer .inner{
        white-space: nowrap;
        text-overflow: ellipsis;
    }
  }

  @media only screen and (max-width: 700px) {
    .sketch {
        padding-top: 2rem;
    }
    .sketchContainer .leftHeader, .sketchContainer .rightHeader {
        font-size:.7rem;
        letter-spacing: .1rem;
    }
    .wireframe-container, .sketch-container, .prototype-container, .develop-container {
        padding: 2rem;
    }
  }