html, body {
  min-height:100%;
  padding:0; 
  margin:0;
  overflow-y: visible; 
}
a {
    color:#000000;
    text-decoration: none;
}
body div.home {
  height:97vh;
  width:97vw;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: .1rem;
}
.home {
    background-color:#ffffff;
    padding-left: 2vw;
}

.titleBar {
    position:relative;
    top: 11px;
    left: -1vw;
    font-size: 2rem;
}

.titleBar, .titleBar a {
    text-align: center;
    color:#3f3f3f;
}

div svg.homeNav {
  width: 24vw;
  height: 100%;
  overflow: visible;
  min-width: 123px;
}

.textContainer {
  position:relative;
  top: -51.5%;
}

div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop  {
    width: 24.8%;
    display:inline-block;
    text-align: center;
}

div svg circle {
  cx: 50%;
  cy: 50%;
  r: 25%;
  stroke: none;
  transition: r .5s;
}

div svg.nav {
  width: 2rem;
  height: 2rem;
  overflow: visible;
  position:relative;
  top:-1.80rem;
  left:1.4rem;
}

div svg circle.nav {
  cx: 50%;
  cy: 50%;
  r: 25%;
  stroke: none;
  transition: r .5s;
}

a:focus svg circle {
    r: 100%;
}

a.link-sketch svg circle {
    fill: #5ADF8E;
}

a.link-wireframe svg circle {
  fill: #F881B9;
}

a.link-prototype svg circle {
  fill: #49DCD9;
}

a.link-develop svg circle {
  fill: #F9FB42;
}

a svg circle:hover {
    opacity:.8;
    transition: r 1s;
    r: 40%;
}

.backArrow {
  margin-bottom:2rem;
  display:inline;
  font-size:1.5rem;
  line-height:1.5rem;
  transition: font-size .5s;
}

.backArrow:hover {
  font-size: 2rem;
}

@media only screen and (max-width: 600px) {
  body div.home {
    height:unset;
    width:unset;
  }
  .titleBar {
    font-size:.6rem;
    padding-bottom: 2rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    letter-spacing: .04rem;
  }
  .tinyNav {
    display:inline;
    position:relative;
    top:2.3rem;
    left:-1rem;
  }
  .textContainer {
    position:absolute;
    top: 5.3rem;
    width:100%;
  }
  .home svg.homeNav {
    display:block;
    height: unset;
    margin-left: auto;
    margin-right:auto;
    margin-top:0rem;
    margin-bottom:4rem;
  }
    .home svg circle {
      r: 50%;
    }
    .home svg circle:hover {
      r: 60%;
    }
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      width:100%;
      height: 11.2rem;
    }
  }


@media only screen and (max-width: 1200px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      font-size: 0.7rem;
    }
  }


@media only screen and (max-width: 1200px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      font-size: 0.7rem;
    }
  }

  @media only screen and (min-height: 1600px) {
    div.dispTitleSketch, div.dispTitleWireframe,div.dispTitlePrototype, div.dispTitleDevelop {
      top:-51%;
    }
  }