.prototype-container {
    background-color: #49DCD9;
    padding:1rem 5rem 5rem 5rem;
    min-height:100vh;
}
.prototype{
    font-size: 1.4rem;
    font-weight: 700;
  letter-spacing: .1rem;
  margin-bottom: 3rem;
}

p {
    padding-top:4%
}

iframe {
    border-radius: 2.9rem;
}

.prototype-container .innerBorder {
    background-color:rgba(255,255,255,.5);
}
.prototype-container .innerBorder img {
    margin:2rem;
    max-width:80%;
    border-radius: 0%;
}



@media only screen and (max-width: 700px) {
    .prototype {
        padding-top:2rem;
    }
    .prototype-container {
        padding: 2rem;
    }
    iframe {
        max-height:200px;
    }
}